<template>
  <div>
    <section class="top">
      <tab :yearAchtive="yearAchtive" @onclick="selectTime" :data='this.getRespone.tabJsons'/>
      <div class="veRing">
        <ve-ring :data="chartJson" width="98%"></ve-ring>
      </div>
    </section>
    <achieveReport :data="vipJson" />
  </div>
</template>

<script>
import tab from "@/components/tab";
import veBar from "@/components/veBar";
import achieveReport from "@/components/report";
export default {
  data() {
    return {
      chartData: this.getRespone.json,
      vipJson: [],
      chartJson:this.chartJson,
      yearAchtive: 1
    };
  },
  mounted() {
    this.getVip();
    this.getChartJson();
  },
  methods: {
    getVip() {
      var key = this.getRespone.vipKey;
      var val = this.getRespone.achieveVal;
      var achieve = [];
      key.forEach(key => {
        val.forEach(val => {
          if (key.id === val.id) {
            achieve.push({ key, val });
          }
        });
      });
      this.vipJson = achieve;
    },
    getClock() {
      var key = this.getRespone.clockKey;
      var val = this.getRespone.achieveVal;
      var achieve = [];
      key.forEach(key => {
        val.forEach(val => {
          if (key.id === val.id) {
            achieve.push({ key, val });
          }
        });
      });
      this.clockJson = achieve;
    },
    selectTime(e) {
      var year = this.util.getTime(new Date()).substring(0, 4);
      var month = this.util.getTime(new Date()).substring(5, 7);
      var day = this.util.getTime(new Date()).substring(8, 10);
      this.yearAchtive = e;
    },
    getChartJson() {
      this.chartJson = this.vipJson;
      var rows = [];
      var rowArr = {};
      var columns = ['name','key'];
      this.chartJson.forEach((el, index) => {
        var name = el.key.name;
        if (name !== "") {
          rows.push({ name: el.key.name, key: el.val.name });
        }
      });

      this.chartJson = { columns, rows };
    }
  },
  components: {
    tab,
    veBar,
    achieveReport
  }
};
</script>

<style lang="scss" scoped>
.top {
  background: $fc;
  padding: 0.8rem 0rem;
}
.veRing {
  margin: 0.8rem;
  padding: 0.5rem;
  // border: 0.02rem solid $Tline;
  border-radius: 0.5rem;
  box-shadow: 0rem 0.3rem 0.8rem $Tline;
  background-color: $fc;
}
</style>